r el esfuerzo que realizan los paneleros, buscando la manera de aumentar el rendimiento en la obtención del jugo extraído a partir de la caña, para superar la cifra promedio que oscila alrededor del 35 % y, así, recibir mayores beneficios económicos, aumentando la producción promedio diaria. Por esto es necesaria la mejora en los eventos de extracción del jugo, aprovechándolos al máximo antes de llegar al producto de desecho llamado bagazo, el cual presenta una contextura con exceso de humedad, siendo su composición física 50 % de humedad, 5 % de sólidos solubles y un 45 % de sólidos insolubles. Es un reto de cada unidad panelera donde la pérdida de jugo al ser desechado junto con el bagazo evidencia un problema del proceso productivo de la panela. Al mejorar las concentraciones de jugo obtenidos, también se pretende obtener un producto óptimo que facilite las actividades laborales de las personas que se dedican al proceso panelero. Teniendo en cuenta lo anterior, para lograr un adecuado proceso de extracción de jugo de caña, se hace realmente necesario modificar los procesos de extracción implementando algún tipo de proceso en la cadena productiva de la panela, que permita mejorar la extracción al contacto que ejerce la presión con los rodillos del trapiche.")])]),_c('div',{staticClass:"col-md-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/temas/T1/img-8.png"),"alt":"mujer con un laptop y un robot"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("En conclusión, la extracción de jugo consiste en pasar la caña a través del molino para exprimirla y obtener un jugo o guarapo crudo como producto principal, además de bagazo húmedo (verde) que se emplea como combustible para la hornilla. Para determinar el porcentaje de extracción se debe pesar el bagazo, pero se debe tener en cuenta que, debido a que el bag